@import"normalize.css";html{font-size:62.5%;line-height:1.5;scroll-behavior:smooth;box-sizing:border-box;overflow-x:hidden !important;margin:0;padding:0}*,*:before,*:after{box-sizing:inherit}body{font-family:"Lato",sans-serif;background:#dfdad0;width:100% !important;font-size:1.7rem;font-weight:400;position:relative;margin:0;overflow-x:hidden !important}body.noscroll{overflow:auto !important;position:fixed}*,*:before,*:after{box-sizing:inherit}.subtle{color:#aaa}.cards-wrapper{max-width:100%;display:flex;flex-wrap:wrap;justify-content:center;gap:2.5rem;margin:2.5rem}.card-container{width:500px}.card-container img{max-height:250px;width:100%;object-fit:cover}.card{background-color:#fff;padding:3rem;position:relative;box-shadow:5px 5px 10px rgba(75,75,75,.2039215686);z-index:1;border-radius:4rem}.card-body{display:inline-block}.card-circle{border:1px solid #aaa;border-radius:50%;height:5rem;width:5rem;display:flex;justify-content:center;align-items:center}.card-number{font-size:2rem}.card-author{display:block;font-size:1.5rem;letter-spacing:.5px;margin:1.5rem 0 0;text-transform:uppercase}.card-title{font-family:"Cormorant Garamond",serif;font-size:6rem;font-weight:300;line-height:6rem;margin:1rem 0;text-transform:capitalize}.card-description{font-weight:300;line-height:2.2rem;margin:1rem 0}.card-read{cursor:pointer;font-size:1.4rem;font-weight:700;letter-spacing:.6rem;margin:.5rem 0 2rem;position:relative;text-align:right;text-transform:uppercase}.card-read:after{background-color:#b8bddd;content:"";display:block;height:.1rem;position:absolute;top:.9rem;min-width:55%}@media(min-width: 768px){.card-read:after{min-width:75%}}.card-tag{float:right;margin:.5rem 0 0;border:1px solid #aaa;padding:.5rem 1rem;cursor:pointer;border-radius:1rem}.card-tag:hover{background:#aaa;color:#000}.card-media{max-width:100%;height:auto}.card-shadow{background-color:#fff;box-shadow:0 2px 25px 2px #000,0 2px 50px 2px #000,0 0 100px 3px rgba(0,0,0,.25);height:1px;margin:-1px auto 0;width:80%;z-index:-1}@media(max-width: 998px){.main-card--cointainer{display:grid;grid-template-columns:repeat(1, minmax(200px, 500px));grid-gap:5rem;justify-content:space-around}}.navbar{margin:10rem 0 8rem;text-align:center}.navbar span{padding-bottom:2rem;text-transform:capitalize;font-size:2rem;font-weight:600}.btn-group{border-radius:1rem;text-transform:capitalize;display:flex;justify-content:center;flex-wrap:wrap;gap:1rem}@media(min-width: 768px){.btn-group{gap:0}}.btn-group__item{border:none;padding:1.5rem 3rem;background-color:#fff;cursor:pointer;margin:0;box-shadow:inset 0px 0px 0px -15px #639;transition:all 300ms ease-out;text-transform:capitalize}.btn-group__item:last-child{border-top-right-radius:1rem;border-bottom-right-radius:1rem}.btn-group__item:first-child{border-top-left-radius:1rem;border-bottom-left-radius:1rem}.btn-group__item:hover,.btn-group__item:focus{color:#639;box-shadow:inset 0px -20px 0px -15px #639}.btn-group__item:focus{outline:none}.btn-group__item:after{content:"✔️";margin-left:.5rem;display:inline-block;color:#639;position:absolute;transform:translatey(10px);opacity:0;transition:all 200ms ease-out}.btn-group__item--active:after{opacity:1;transform:translatey(-2px)}